<template>
  <div class="admin">
    <router-link to="/add">
      <el-button type="primary">添加项目</el-button>
    </router-link>
    <router-link to="/addimg">
      <el-button type="primary">添加图片</el-button>
    </router-link>
    <span class="type-select">
      <el-select v-model="value" placeholder="请选择" @change="selectChange">
        <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
        </el-option>
      </el-select>
    </span>
    <router-link to="imgbox">
      <el-button type="primary">图片库</el-button>
    </router-link>
    <div class="my-table">
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column prop="id" label="ID" width="180">
        </el-table-column>
        <el-table-column prop="title" label="标题" width="180">
        </el-table-column>
        <el-table-column prop="author" label="作者">
        </el-table-column>
        <el-table-column prop="type" label="类型">
           <template slot-scope="scope">
              <span>{{proType[scope.row.type-1]}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="listimg" label="列表图片">
          <template slot-scope="scope">
              <router-link :to="{name:'ImgBox',query:{id:scope.row.id,type:1}}">
                <el-button type="text" size="small">添加</el-button>
              </router-link>
            <img class="list-img" :src="`http://go.richfly.cn/${scope.row.listImg}`" alt="" >
          </template>
        </el-table-column>
        <el-table-column prop="introimg" label="介绍图片">
          <template slot-scope="scope">
              <router-link :to="{name:'ImgBox',query:{id:scope.row.id,type:2}}">
                <el-button type="text" size="small">添加</el-button>
              </router-link>
            <img class="list-img" :src="`http://go.richfly.cn/${scope.row.introImg}`" alt="">
          </template>
        </el-table-column>
        <el-table-column prop="images" label="内容图片">
          <template slot-scope="scope">
              <router-link :to="{name:'ImgBox',query:{id:scope.row.id,type:0}}">
                <el-button type="text" size="small">添加</el-button>
              </router-link>
            <img class="list-img" :src="`http://go.richfly.cn/${item}`" alt="" v-for="item in scope.row.images" :key="item">
          </template>
        </el-table-column>
        <el-table-column prop="images" label="操作">
            <template slot-scope="scope">
                <el-button type="text" size="small">修改</el-button>
            </template>
          </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "HelloWorld",
  data() {
    return {
      proType:["品牌","UI","网页","H5","平板"],
      tableData: [],
      options: [
        {
          value: "1",
          label: "1"
        },
        {
          value: "2",
          label: "2"
        }
      ],
      value: ""
    };
  },
  methods: {
    _request: function(type) {
      var self = this;
      var url;
      if (type) {
        url = `/getPages?size=100&type=${type}`;
      } else {
        url = "/getPages?size=100";
      }
      axios.get(url).then(function(res) {
        self.tableData = res.data;
      });
    },
    selectChange: function(val) {
      this._request(val);
    }
  },
  created: function() {
    this._request();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less">
.type-select {
  margin-left: 30px;
}

.my-table {
  margin-top: 30px;
}

.list-img {
  width: 30px;
  height: 30px;
  margin-left: 20px;
}
</style>



