<template>
<div class="img-box">
    <el-row>
      <el-col :span="4">
        <el-button type="primary" @click="addImg">确认添加</el-button>
      </el-col>
      <el-col :span="8">
        <el-input placeholder="请输入关键词" v-model="searchkey" class="input-with-select">
          <el-button slot="append" icon="el-icon-search" @click="goSearch"></el-button>
        </el-input>
      </el-col>
    </el-row>
    <div class="img-con">
      <el-col :span="4" v-for="(item,index) in Images" :key="item.src">
        <el-badge class="badge-icon" :value="index+1"  :hidden="item.badge">
          <el-card :body-style="{ padding: '0px' }" >
            <img :src="`http://go.richfly.cn/${item.src}`" class="image" @click="handleimg(item,index)">
            <div style="padding: 14px;">
              <span>{{item.keyword}}</span>
              <!-- <div class="bottom clearfix">
              <time class="time">{{ currentDate }}</time>
              <el-button type="text" class="button">操作按钮</el-button>
            </div> -->
            </div>
          </el-card>
        </el-badge>
      </el-col>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { orderBy } from "lodash";
export default {
  name: "HelloWorld",
  data() {
    return {
      Images: [],
      currentDate: new Date(),
      searchkey: "",
      uuid: 0
    };
  },
  computed: {
    imgGroup() {
      return this.Images.filter(o => !o.badge);
    }
  },
  methods: {
    _request(key) {
      let src = key ? `/getImg?key=${key}` : "/getImg";
      axios.get(src).then(res => {
        console.log(res.data);
        this.Images = res.data.map(o => {
          o.badge = true;
          o.uuid = 0;
          return o;
        });
      });
    },
    goSearch() {
      this._request(this.searchkey);
    },
    _handleType() {
      const { type = 0 } = this.$route.query;
      const len = this.imgGroup.length;
      if ([1, 2].indexOf(Number(type)) != -1) {
        if (len > 0) {
          console.log(2);
          this.$message({
            message: `${type == 1 ? "列表" : "介绍"}图片只能选择一张`,
            type: "warning"
          });
          return true;
        }
      }
      return false;
    },
    handleimg: function(o, i) {
      const typeVervify = this._handleType();
      if (typeVervify) return false;
      this.uuid++;
      o.badge = !o.badge;
      if (o.badge) {
        o.num = 1000;
      } else {
        o.num = this.uuid;
      }
      this.Images = orderBy(this.Images, ["num"], ["asc"]);
    },
    async addImg() {
      if (this.imgGroup.length === 0) {
        this.$message({
          message: "请选择图片",
          type: "warning"
        });
        return false;
      }
      const { id, type } = this.$route.query;
      const Images = this.imgGroup.map(o => o.src);
      const res = await axios.post("/setImages", {
        Id: id,
        Type: type,
        Images
      });
      if (res.status === 201) {
        this.$router.push("/");
      }
    }
  },
  created() {
    this._request();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less">
.time {
  font-size: 13px;
  color: #999;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.button {
  padding: 0;
  float: right;
}

.image {
  width: 100%;
  display: block;
  height: 210px;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both;
}

.img-con {
  margin-top: 30px;
  padding-top: 30px;
  overflow: hidden;
  > div {
    margin-top: 30px;
  }
}

.el-badge__content.is-fixed {
  z-index: 9;
  right: 40px;
}
</style>



