import Vue from 'vue'
import Router from 'vue-router'
import Admin from '@/views/Admin'
import Add from '@/views/Add'
import AddImg from '@/views/AddImg'
import ImgBox from '@/views/ImgBox'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Admin',
      component: Admin
    },
    {
      path: '/add',
      name: 'Add',
      component: Add
    },
    {
      path: '/addimg',
      name: 'AddImg',
      component: AddImg
    },
    {
      path: '/imgbox',
      name: 'ImgBox',
      component: ImgBox
    }
  ]
})



