<template>
  <div class="add">

    <el-form ref="form" :model="form" label-width="80px">
      <el-form-item label="标题">
        <el-input v-model="form.title"></el-input>
      </el-form-item>
      <el-form-item label="作者">
        <el-input v-model="form.author"></el-input>
      </el-form-item>
      <el-form-item label="类型">
        <el-radio v-model="form.type" label="1">品牌</el-radio>
        <el-radio v-model="form.type" label="2">UI</el-radio>
        <el-radio v-model="form.type" label="3">网页</el-radio>
        <el-radio v-model="form.type" label="4">H5</el-radio>
        <el-radio v-model="form.type" label="5">平板</el-radio>
      </el-form-item>
      <el-form-item label="关键字">
        <el-input placeholder="请输入内容" v-model="form.keywords"></el-input>
      </el-form-item>
      <el-form-item label="内容">
        <el-input type="textarea" :rows="2" placeholder="请输入内容" v-model="form.content"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">确定</el-button>
        <el-button>取消</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "HelloWorld",
  data() {
    return {
      dialogImageUrl: "",
      dialogVisible: false,
      isImageShow: false,
      form: {
        title: "",
        author: "",
        type: "1",
        keywords: "",
        content: ""
      },
      List: []
    };
  },
  methods: {
    async onSubmit(){
      var self = this;
      const send = Object.assign(this.form,{
        uuid:Math.ceil(Date.now()),
        type:Number(this.form.type)
      })
      const res = await axios.post("/addPage",send);
      if(res.status === 201){
        this.$router.push("/");
      };
    }
  },
  created: function() {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less">
.type-select {
  margin-left: 30px;
}

.my-table {
  margin-top: 30px;
}

.list-img {
  width: 30px;
  height: 30px;
  margin-left: 20px;
}
</style>



