<template>
  <div class="add-img">
    <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="关键词">
            <el-input v-model="form.key"></el-input>
          </el-form-item>
      <el-form-item label="图片上传">
        <el-upload 
        ref="upload" 
        action="/" 
        list-type="picture-card" 
        :http-request="setRequest" 
        :auto-upload="true" 
        :on-preview="handlePreview"
        :on-change="handleChange"
        :on-remove="handleRemove">
          <i class="el-icon-plus"></i>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible">
          <img width="100%" :src="dialogImageUrl" alt="">
        </el-dialog>
      </el-form-item>
      <router-link to="/imgbox">
        <el-button type="primary">进入图库</el-button>
      </router-link>
    </el-form>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "HelloWorld",
  data() {
    return {
      dialogImageUrl: "",
      dialogVisible: false,
      isImageShow: false,
      uuid: 1,
      form: {
        key: ""
      },
      List: []
    };
  },
  methods: {
    setRequest: function(content) {
      let formData = new FormData();
      formData.append("file", content.file);
      formData.append("keyword", this.form.key);
      let config = {
        "Content-Type": "multipart/form-data"
      };
      let var_this = this;
      axios
        .post("/updateImg", formData, config)
        .then(function(response) {
          console.log(response);
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    onSubmit: function() {
      // var self = this;
      // var form = this.form;
      // axios.post("/addPage", form).then(
      //   function (data) {
      //     if (data.status === 201) {
      //       self.uuid = data.data.id;
      //       self.isImageShow = true;
      //     }
      //   }).catch(function (err) {
      //   console.log(err);
      // })
    },
    handleChange: function(file, fileList) {
      this.List = fileList;
      console.log(file, fileList);
    },
    handleRemove: function(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview: function(file) {
      console.log(file);
    },
    handleBefore: function(file, fileList) {
      console.log(file, fileList);
    }
  },
  created: function() {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less">
.type-select {
  margin-left: 30px;
}

.my-table {
  margin-top: 30px;
}

.list-img {
  width: 30px;
  height: 30px;
  margin-left: 20px;
}
</style>



